import React from "react";
import { ZliveForm } from "zerod";
import { Button } from "antd";
import HOC from "@/HOC/load-HOC.js";
const { AmdDocHOC, AshowDemoHOC } = HOC;
import doc from "zerod/components/ZliveForm/doc.md";
import formData from "./formData";
import linkages from "./linkages";
export default AmdDocHOC(doc, {
	demo1: function() {
		class Myjavascript extends React.PureComponent {
			formData = { ...formData, linkages };
			onSubmit = (values) => {
				console.log(values);
				return Promise.resolve();
			};
			render() {
				return <ZliveForm.FormViewer formData={this.formData} onSubmit={this.onSubmit} />;
			}
		}
		const Dom = AshowDemoHOC(<Myjavascript />, true);
		return <Dom />;
	},
	demo2: function() {
		class Myjavascript extends React.PureComponent {
			render() {
				return <span />;
			}
		}
		return <Myjavascript />;
	},
});
